html {
	font-size: 100px;
	font-size: 5.208334vw;
	overflow-x: hidden;
	&.overflowY {
		overflow-y: hidden; } }

body {
	background-color: $black;
	color: $white;
	* {
		font-size: 1em;
		font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
		line-height: 1.4em; } }

a {
	display: flex;
	text-decoration: none; }

section {
	width: 11.4em;
	margin: 0 auto; }

#header-stick {
	width: 100%;
	&.sticky {
		top: 0;
		position: fixed;
		z-index: 10; } }

#header-sec-two {
	background-color: $black;
	width: 100%; }
.header {
	background-color: $gold;
	section {
		width: 100%; }
	&__contacts {
		padding: .1em 1.2em;
		display: flex;
		align-items: center;
		button {
			display: flex;
			margin: 0;
			padding: .05em .15em;
			cursor: pointer;
			background-color: $black;
			border-radius: .05em;
			border: none;
			transition: all .5s cubic-bezier(0.62, 0.2, 0.4, 0.83);
			&:hover, &:active {
				transform: scale(1.05);
				p {
					color: $hh-text; } }
			p {
				user-select: none;
				transition: all .5s cubic-bezier(0.62, 0.2, 0.4, 0.83);
				color: $gold;
				line-height: 1;
				font-size: .14em; } } }
	&__link {
		display: flex;
		flex-flow: column;
		margin-right: auto;
		a {
			color: $black;
			font-size: .14em;
			transition: all .5s cubic-bezier(0.62, 0.2, 0.4, 0.83);
			&:hover, &:focus {
				box-shadow: 0 0.07142em 0 $black; } } }
	&__section-two {
		width: 11.4em;
		margin: 0 auto;
		padding-top: .08em;
		display: flex;
		flex-flow: column;
		align-items: center; }
	&__logo {
		display: flex;
		align-items: center;
		justify-content: center;
		picture {
			display: flex;
			img {
				width: 3em;
				height: .55em; } } }
	&__hr {
		margin: .08em 0;
		width: 100%;
		height: .01em;
		background-color: #fff; }
	&__nav {
		position: relative;
		padding-bottom: .08em;
		ul {
			padding: 0 .5em; }
		#sub-menu {
			transform: scaleY(0);
			transform-origin: 0 0;
			top: .47em;
			position: absolute;
			text-align: left;
			background: rgba(0, 0, 0, .8);
			padding: 0 .15em;
			z-index: 10;
			border-radius: .02em;
			transition: all .5s cubic-bezier(0.62, 0.2, 0.4, 0.83);
			li {
				text-shadow: $black 1px 1px 0, $black -1px 1px 0, $black 1px -1px 0, $black -1px -1px 0;
				margin: .05em 0;
				a {
					color: $white;
					transform: none;
					&:hover {
						color: $hh-text; } } } }
		ul {
			display: flex;
			li {
				display: flex;
				align-items: center;
				&:not(:last-child) {
					margin-right: 0.35em; }
				&:hover, &:focus {
					svg {
						transform: scale(1.2); }
					path {
						fill: $hh-text;
						transform: rotate(30deg) translate(.01em, -.0165em); }
					#sub-menu {
						transform: scaleY(1); }
					a {
						color: $hh-text;
						transform: scale(1.1); } }
				a {
					color: $white;
					font-size: .16em;
					font-weight: bold;
					transition: all .5s cubic-bezier(0.62, 0.2, 0.4, 0.83); }
				svg {
					margin-left: .05em;
					margin-top: .04em;
					width: .07em;
					height: .07em;
					transition: all .5s cubic-bezier(0.62, 0.2, 0.4, 0.83);
					path {
						transition: all .5s cubic-bezier(0.62, 0.2, 0.4, 0.83); } }
				//for sub-menu
				ul {
					display: flex;
					flex-flow: column; } } } } }

.mask {
	opacity: 0;
	position: fixed;
	top: 0;
	left: 0;
	z-index: 0;
	width: 100vw;
	height: 100vw;
	background-color: rgba(0, 0, 0, .7);
	cursor: pointer;
	transition: all .5s cubic-bezier(0.62, 0.2, 0.4, 0.83);
	pointer-events: none;
	&.active {
		pointer-events: auto;
		z-index: 10;
		opacity: 1; } }
.modalW {
	opacity: 0;
	z-index: -1;
	position: fixed;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	background-color: $black;
	border-radius: .2em;
	border: .01em solid silver;
	transition: all .5s cubic-bezier(0.62, 0.2, 0.4, 0.83);
	&.active {
		z-index: 100;
		opacity: 1; }
	#close {
		z-index: 100;
		position: absolute;
		width: 0.45em;
		height: 0.45em;
		border-radius: 50%;
		background-color: rgba(0, 0, 0, .7);
		outline: none;
		border: .01em solid rgba(0, 0, 0, 0);
		transition: all .5s cubic-bezier(0.62, 0.2, 0.4, 0.83);
		cursor: pointer;
		right: -.4em;
		top: -.4em;
		&:hover, &:focus {
			border: .01em solid #ff7a0d; }
		p {
			color: $white;
			font-size: .2em; } }
	label, input, textarea {
		font-size: .16em; }
	&-h2 {
		margin-top: .15em;
		margin-bottom: .15em;
		h2 {
			text-align: center;
			font-size: .18em; } }
	.hr {
		height: .01em;
		background-color: silver; }
	.forma {
		width: 4em;
		padding: .01em .5em .2em;
		display: grid;
		grid-auto-flow: row;
		gap: .1em 0;
		input, textarea {
			padding: 0 .3125em;
			border: .01em solid transparent; }
		input {
			&.invalid-phone {
				border: .01em solid red; } }
		&-group {
			display: grid;
			grid-auto-flow: row;
			gap: .05em 0;
			&:first-child {
				margin-top: 0.1em; } }
		&-button {
			display: flex;
			position: relative;
			left: 50%;
			transform: translateX(-50%);
			width: max-content;
			justify-content: center;
			user-select: none;
			background-color: silver;
			background-color: transparent;
			button {
				display: flex;
				border: none;
				border-radius: .05em;
				padding: .05em .1em;
				cursor: pointer;
				transition: all .5s cubic-bezier(0.62, 0.2, 0.4, 0.83);
				p {
					user-select: none;
					font-weight: 600;
					font-size: .16em;
					display: flex;
					align-items: center;
					&::before {
						content: '';
						display: block;
						background: url(/img/icons/telegram.svg) no-repeat;
						background-size: contain;
						background-position: 0 0;
						width: 1em;
						height: 1em;
						margin-right: .625em; } }
				&.active {
					background-color: $hh-text; } } } }
	.user-agree {
		display: flex;
		&.active {
			color: $hh-text;
			text-decoration: underline; }
		input {
			display: none;
			&:checked {
				+ label {
					&::before {
						content: '';
						display: flex;
						align-items: center;
						justify-content: center;
						background: url(/img/icons/check-mark.svg) no-repeat;
						background-size: 80%;
						background-position: 50% 50%;
						background-color: $hh-text;
						padding: .01em; }
					a {
						color: $hh-text;
						text-decoration: underline;
						pointer-events: auto; } } } }
		label {
			font-size: 1em;
			position: relative;
			display: flex;
			text-align: justify;
			&::before {
				content: '';
				display: flex;
				padding: .01em;
				margin-top: .035em;
				display: flex;
				position: absolute;
				transition: all .5s cubic-bezier(0.62, 0.2, 0.4, 0.83);
				width: 0.13em;
				height: 0.13em;
				background-color: #fff; }
			a {
				pointer-events: none;
				transition: all .5s cubic-bezier(0.62, 0.2, 0.4, 0.83);
				color: white;
				display: contents; }
			p {
				margin-left: 1.5em;
				font-size: .14em; } } } }

// CSS for About section
#about {
	&.sticky {
		margin-top: 1.5em; } }
.about {
	display: flex;
	position: relative;
	flex-flow: column;
	&__h1 {
		display: flex;
		justify-content: center;
		margin-top: .8em;
		margin-bottom: .9em;
		h1 {
			font-size: .36em;
			font-weight: bold; } }
	&__video {
		video {
			float: left;
			width: 3.2em;
			height: 2.8em;
			margin-right: .4em; } }
	&__description {
		p {
			font-size: .16em;
			&:not(:last-child) {
				margin-bottom: 1em; }
			a {
				text-decoration: underline;
				color: $link;
				display: inline-flex; } }
		ul {
			margin-bottom: 0.2em;
			margin-left: 3.7em;
			li {
				list-style-type: '- ';
				font-size: .16em; } } } }

//CSS for Vacancy
.vacancy {
	width: 11.4em;
	margin: 0 auto;
	&__h2 {
		text-align: center;
		margin-top: 0.45em;
		margin-bottom: .4em;
		h2 {
			font-size: .36em; } }
	.pre {
		font-size: .16em; }
	&-slider {
		display: flex;
		position: relative;
		text-align: center;
		margin-top: .4em;
		p {
			font-size: .16em; } }
	&-slide {
		margin-right: auto;
		position: relative;
		picture {
			position: relative;
			display: flex;
			img {
				width: 2.7em;
				height: 2.3em;
				margin-bottom: .25em; } }
		&__bg {
			position: absolute;
			display: flex;
			align-items: center;
			justify-content: center;
			top: 0;
			left: 0;
			width: 2.7em;
			height: 2.3em;
			transition: all .5s cubic-bezier(0.62, 0.2, 0.4, 0.83);
			.round {
				opacity: 0;
				position: relative;
				display: flex;
				align-items: center;
				justify-content: center;
				width: 1.87em;
				height: 1.87em;
				border-radius: 50%;
				border: .01em solid $white;
				cursor: pointer;
				transition: all .5s cubic-bezier(0.62, 0.2, 0.4, 0.83);
				button {
					outline: none;
					cursor: pointer;
					border: none;
					background-color: transparent;
					p {
						user-select: none;
						text-transform: uppercase;
						color: $white; } } } }
		&:hover {
			.round {
				opacity: 1; }
			.vacancy-slide__bg {
				background-color: rgba(0, 0, 0, .4); } } }
	&-modal {
		opacity: 0;
		position: fixed;
		display: flex;
		flex-flow: column;
		top: 50%;
		left: 50%;
		z-index: 20;
		border: .01em solid #A9A9A9;
		background-color: $black;
		width: 7.26em;
		height: 3.8em;
		align-items: center;
		justify-content: center;
		transform: translate(-50%, -50%);
		transition: all .5s cubic-bezier(0.62, 0.2, 0.4, 0.83);
		pointer-events: none;
		&.active {
			pointer-events: auto;
			opacity: 1; }
		&__h2 {
			h2 {
				display: flex;
				flex-flow: column;
				width: max-content;
				font-size: 0.32em;
				font-weight: bold; } }
		.forma {
			display: grid;
			grid-auto-flow: row;
			gap: .3em 0;
			margin-top: 0.38em;
			section {
				display: grid;
				grid-auto-flow: column;
				gap: 0 .4em;
				width: 4.4em; }
			input {
				padding: .46714em 0 .46714em 1.07142em;
				font-size: .14em;
				width: 100%;
				border: none;
				cursor: pointer;
				&::placeholder {
					font-weight: 600;
					color: #000; } }
			button {
				cursor: pointer;
				padding: 0;
				margin: 0;
				display: flex;
				width: 2em;
				height: 0.33em;
				align-items: center;
				justify-content: center;
				border: none;
				background: linear-gradient(180deg, #FFC300 0%, #FF7304 51.43%);
				p {
					user-select: none;
					color: $white;
					font-weight: bold;
					font-size: .14em; } } } } }

//CSS for Why u should choose us
.choose {
	display: flex;
	flex-flow: column;
	width: 9em;
	margin: 0 auto;
	&__h2 {
		margin-top: 1em;
		margin-bottom: .8em;
		text-align-last: center;
		h2 {
			font-weight: bold;
			font-size: .36em; } }
	&-container {
		display: flex;
		justify-content: center; }
	&__card {
		display: flex;
		flex-flow: column;
		&:not(:last-child) {
			margin-right: auto; }
		picture {
			justify-content: center;
			display: flex;
			img {
				width: 1.04em;
				height: 1.04em; } }
		p {
			display: flex;
			flex-flow: column;
			align-items: center;
			font-size: .16em;
			span {
				display: flex; } } } }

