#map-section {
	width: 100%; }
.map {
	margin-top: 0.6em;
	width: 100%;
	height: 5em;
	position: relative;
	.ymaps-2-1-77-copyrights-pane {
		display: none; }
	#ymap_ctrl_display {
		display: none;
		width: 100%;
